// import * as utils from '@nervosnetwork/ckb-sdk-utils';
import * as React from 'react';
import './App.css';
import Generation from './components/Generate';
import NewKeyAndLock from './components/NewKeyAndLock';
import NewLock from './components/NewLock';

const App = () => {
  const [content, setContent] = React.useState(() => <div />)

  const onNewKeyAndLock = () => {
    setContent!(() => <NewKeyAndLock />)
  }
  const onNewLock = (privatekey: string) => {
    setContent!(() => <NewLock privateKey={privatekey} />)
  }
  const initContent = () => <Generation onNewKeyAndLock={onNewKeyAndLock} onNewLock={onNewLock} />

  React.useEffect(() => {
    setContent(initContent)
  }, [])

  return (
    <div className="App">
        <h1>Nervos Key and Lock Generation Tool</h1>
        {content}
      </div>
  )
}

export default App;



