import { SHA3 } from 'sha3';
import bitcoin_unlock_hash from '../rubyScripts/bitcoin_unlock_hash';
import bytesToHex from '../utils/BytesToHex';
import hexToBytes from '../utils/HexToBytes';
import stringToHex from '../utils/StringToHex';

export default (publicKey: string) => {
  const reference = "0x2165b10c4f6c55302158a17049b9dad4fef0acaf1065c63c02ddeccbce97ac47" 
  const signedArgs: string[] = [
    bitcoin_unlock_hash,
    stringToHex(publicKey)
  ]
  const hash = new SHA3(256)
  hash.update(new Buffer(hexToBytes(reference)))
  hash.update('|')
  for (const iterator of signedArgs) {
    hash.update(new Buffer(hexToBytes(iterator)))
  }
  const result = hash.digest()
  return bytesToHex(result)
}


