
// tslint:disable
const bytesToHex = (bytes: Uint8Array): string => {
  const hex = []
  for (let i = 0; i < bytes.length; i++) {
    hex.push((bytes[i] >>> 4).toString(16))
    hex.push((bytes[i] & 0xf).toString(16))
  }
  return hex.join('')
}
// tslint:enable

export default bytesToHex


