import * as ECPair from '@nervosnetwork/ckb-sdk-utils/lib/ecpair';
import * as React from 'react';
import bytesToHex from '../utils/BytesToHex';
import verifyScript from '../utils/VerifyScript'

export default () => {
  const pair = ECPair.makeRandom()

  // const key = "0x212f030aead24abab79ab84d28d42c3bc4121fc4d21c7d1ec726481efb6d6856"
  // const bKey = hexToBytes(key)
  // const pair = new ECPair.default(new Buffer(bKey))

  const privateKey = bytesToHex(pair.privateKey)
  const publicKey = bytesToHex(pair.publicKey)
  const address = verifyScript(publicKey)
  
  return (
    <div>
      <p>Please remember the information below, you will not see them again.</p>
      <p>Check here for info about how to use them.</p>
      <h1>Lock Hash</h1>
      <input style={{width: "600px", height: "30px"}} type="text" value={address} readOnly={true} />
      <h1>Private Key</h1>
      <input style={{width: "600px", height: "30px"}} type="text" value={privateKey} readOnly={true} />
    </div>
  )
}


