import ECPair from '@nervosnetwork/ckb-sdk-utils/lib/ecpair';
import * as React from 'react';
import bytesToHex from '../utils/BytesToHex';
import hexToBytes from '../utils/HexToBytes';
import verifyScript from '../utils/VerifyScript'

export default ({ privateKey }: {privateKey: string}) => {  
  const pair = new ECPair(new Buffer(hexToBytes(privateKey)))
  const publicKey = bytesToHex(pair.publicKey)
  const address = verifyScript(publicKey)

  return (
    <div>
      <p>Check here for more info about how to use this lock hash.</p>
      <h1>Lock Hash</h1>
      <input style={{width: "600px", height: "30px"}} type="text" value={address} readOnly={true} />
    </div>
  )
}



