export default (str: string) => {
  if(str === "") {
    return "";
  }
  const hexCharCode = [];
  hexCharCode.push("0x"); 
  for(let i = 0; i < str.length; i++) {
    hexCharCode.push((str.charCodeAt(i)).toString(16));
  }
  return hexCharCode.join("");
}


